import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../RequiredAsterisk";
import isEmpty from "../isEmptyFunction"

const BillingAddress = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Billing Address
      <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <br></br>
      Address: <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <Input
        className="main-input"
        placeholder="Street Address"
        onChange={e =>
          dispatch({
            type: "SET_BILLING_ADDRESS",
            billingAddress: e.target.value
          })
        }
        value={business.billingAddress}
        error={isEmpty(business.billingAddress)}
      ></Input>
      <br></br>
      <br></br>
      City: <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <Input
        className="main-input"
        placeholder="City"
        onChange={e =>
          dispatch({
            type: "SET_BILLING_CITY",
            billingCity: e.target.value
          })
        }
        value={business.billingCity}
        error={isEmpty(business.billingCity)}
      ></Input>
      <br></br> <br></br>
      State: <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <Input
        className="main-input"
        placeholder="State"
        onChange={e =>
          dispatch({
            type: "SET_BILLING_STATE",
            billingState: e.target.value
          })
        }
        value={business.billingState}
        error={isEmpty(business.billingState)}
      ></Input>
      <br></br> <br></br>
      Zip Code: <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <Input
        className="main-input"
        placeholder="Zip Code"
        onChange={e =>
          dispatch({
            type: "SET_BILLING_ZIP",
            billingZip: e.target.value
          })
        }
        value={business.billingZip}
        error={isEmpty(business.billingZip)}
      ></Input>
    </Paper>
  );
};

export default BillingAddress;
