import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";

const PrimaryContactPhone = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Primary Contact Phone Number (if different than A/P Contact)
      <Input
        className="main-input"
        placeholder="What is your primary contact's phone number?"
        onChange={e =>
          dispatch({
            type: "SET_PRIMARY_CONTACT_PHONE",
            primaryContactPhone: e.target.value
          })
        }
        value={business.primaryContactPhone}
      ></Input>
    </Paper>
  );
};

export default PrimaryContactPhone;
