import React, { useContext } from "react";
import BillingSetupContext from "../../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../../RequiredAsterisk";
import isEmpty from "../../isEmptyFunction"

const BankAddress = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Bank Address
      <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <br></br>
      <div className="address">
        Street Address:<RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <Input
          className="main-input"
          placeholder="Street Address"
          onChange={e =>
            dispatch({
              type: "SET_BANK_ADDRESS",
              bankAddress: e.target.value
            })
          }
          value={business.bankAddress}
          error={isEmpty(business.bankAddress)}
        ></Input>
        <br></br>
        <br></br>
        City: <RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <Input
          className="main-input"
          placeholder="City"
          onChange={e =>
            dispatch({
              type: "SET_BANK_CITY",
              bankCity: e.target.value
            })
          }
          value={business.bankCity}
          error={isEmpty(business.bankCity)}
        ></Input>
        <br></br>
        <br></br>
        State: <RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <Input
          className="main-input"
          placeholder="State"
          onChange={e =>
            dispatch({
              type: "SET_BANK_STATE",
              bankState: e.target.value
            })
          }
          value={business.bankState}
          error={isEmpty(business.bankState)}
        ></Input>
        <br></br>
        <br></br>
        Zip Code:<RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <Input
          className="main-input"
          placeholder="Zip Code"
          onChange={e =>
            dispatch({
              type: "SET_BANK_ZIP",
              bankZip: e.target.value
            })
          }
          value={business.bankZip}
          error={isEmpty(business.bankZip)}
        ></Input>
      </div>
    </Paper>
  );
};

export default BankAddress;
