import React from "react";
import AcctsPayableContact from "./AcctsPayableContact";
import BillingAddress from "./BillingAddress";
import PhoneNumber from "./PhoneNumber";
import FaxNumber from "./FaxNumber";
import EmailAddress from "./EmailAddress";
import PrimaryContact from "./PrimaryContact";
import PrimaryContactEmail from "./PrimaryContactEmail";
import PrimaryContactPhone from "./PrimaryContactPhone";

const ContactUpdate = () => {
  return (
    <div>
      <div className="acctsPayable-billingAddress-Phone">
        <AcctsPayableContact></AcctsPayableContact>
        <BillingAddress></BillingAddress>
      </div>
      <PhoneNumber></PhoneNumber>
      <FaxNumber></FaxNumber>
      <EmailAddress></EmailAddress>
      <PrimaryContact></PrimaryContact>
      <PrimaryContactEmail></PrimaryContactEmail>
      <PrimaryContactPhone></PrimaryContactPhone>
    </div>
  );
};

export default ContactUpdate;
