import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../RequiredAsterisk";

const emailMatchesRegex = (email) => {
  //if there is no match, .match returns null, else it returns an object
  //Regex from https://emailregex.com/ on 1/30/2020
  return null===email.match(/(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/) 
}
const EmailAddress = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Email Address
      <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <Input
        className="main-input"
        placeholder="What is your email address?"
        onChange={e =>
          dispatch({
            type: "SET_EMAIL_ADDRESS",
            emailAddress: e.target.value
          })
        }
        value={business.emailAddress}
        error={emailMatchesRegex(business.emailAddress)}
      ></Input>
    </Paper>
  );
};

export default EmailAddress;