import React, { useContext } from "react";
import BillingSetupContext from "../../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../../RequiredAsterisk";
import isEmpty from "../../isEmptyFunction";

const AccountNumber = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Bank Account Number <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <br></br>
      <Input
        className="main-input"
        placeholder="What is your bank account number?"
        onChange={e =>
          dispatch({
            type: "SET_ACCOUNT_NUMBER",
            accountNumber: e.target.value
          })
        }
        value={business.accountNumber}
        error={isEmpty(business.accountNumber)}
      ></Input>
    </Paper>
  );
};

export default AccountNumber;
