import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";

const PrimaryContact = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Primary Contact (if different than A/P Contact)
      <br></br>
      <Input
        className="main-input"
        placeholder="What is your primary contact's name?"
        onChange={e =>
          dispatch({
            type: "SET_PRIMARY_CONTACT",
            primaryContact: e.target.value
          })
        }
        value={business.primaryContact}
      ></Input>
    </Paper>
  );
};

export default PrimaryContact;
