import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../RequiredAsterisk";
import isEmpty from "../isEmptyFunction"

const AcctsPayableContact = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Accounts Payable Contact Name
      <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <br></br>
      <Input
        className="main-input"
        placeholder="What is your accounts payable contact name?"
        onChange={e =>
          dispatch({
            type: "SET_ACCTS_PAYABLE_CONTACT",
            acctsPayableContact: e.target.value
          })
        }
        value={business.acctsPayableContact}
        error={isEmpty(business.acctsPayableContact)}
      ></Input>
    </Paper>
  );
};

export default AcctsPayableContact;
