import actions from "./Actions";

const reducer = (state, action) => {
  switch (action.type) {
    case actions.SET_BUSINESS_NAME:
      return {
        ...state,
        name: action.name
      };
    case actions.SET_UPDATE_PREFERENCE:
      return {
        ...state,
        updatePreference: action.updatePreference
      };
    case actions.SET_ACCTS_PAYABLE_CONTACT:
      return {
        ...state,
        acctsPayableContact: action.acctsPayableContact
      };
    case actions.SET_BILLING_ADDRESS:
      return {
        ...state,
        billingAddress: action.billingAddress
      };
    case actions.SET_BILLING_CITY:
      return {
        ...state,
        billingCity: action.billingCity
      };
    case actions.SET_BILLING_STATE:
      return {
        ...state,
        billingState: action.billingState
      };
    case actions.SET_BILLING_ZIP:
      return {
        ...state,
        billingZip: action.billingZip
      };
    case actions.SET_PHONE_NUMBER:
      return {
        ...state,
        phoneNumber: action.phoneNumber
      };
    case actions.SET_FAX_NUMBER:
      return {
        ...state,
        faxNumber: action.faxNumber
      };
    case actions.SET_EMAIL_ADDRESS:
      return {
        ...state,
        emailAddress: action.emailAddress
      };
    case actions.SET_PRIMARY_CONTACT:
      return {
        ...state,
        primaryContact: action.primaryContact
      };
    case actions.SET_PRIMARY_CONTACT_EMAIL:
      return {
        ...state,
        primaryContactEmail: action.primaryContactEmail
      };
    case actions.SET_PRIMARY_CONTACT_PHONE:
      return {
        ...state,
        primaryContactPhone: action.primaryContactPhone
      };
    case actions.SET_APPROVAL:
      return {
        ...state,
        approval: action.approval
      };
    case actions.SET_INVOICE_RECEIPT:
      return {
        ...state,
        invoiceReceipt: action.invoiceReceipt
      };
    case actions.SET_CC_NUMBER:
      return {
        ...state,
        ccNumber: action.ccNumber
      };
    case actions.SET_SECURITY_CODE:
      return {
        ...state,
        securityCode: action.securityCode
      };
    case actions.SET_FULL_NAME:
      return {
        ...state,
        fullName: action.fullName
      };
    case actions.SET_CC_BILLING_ADDRESS:
      return {
        ...state,
        ccBillingAddress: action.ccBillingAddress
      };
    case actions.SET_CC_BILLING_CITY:
      return {
        ...state,
        ccBillingCity: action.ccBillingCity
      };
    case actions.SET_CC_BILLING_STATE:
      return {
        ...state,
        ccBillingState: action.ccBillingState
      };
    case actions.SET_CC_BILLING_ZIP:
      return {
        ...state,
        ccBillingZip: action.ccBillingZip
      };
    case actions.SET_AUTOPAY_PREFERENCE:
      return {
        ...state,
        autoPayPreference: action.autoPayPreference
      };
    case actions.SET_ROUTING_NUMBER:
      return {
        ...state,
        routingNumber: action.routingNumber
      };
    case actions.SET_ACCOUNT_NUMBER:
      return {
        ...state,
        accountNumber: action.accountNumber
      };
    case actions.SET_BANK_NAME:
      return {
        ...state,
        bankName: action.bankName
      };
    case actions.SET_BANK_ADDRESS:
      return {
        ...state,
        bankAddress: action.bankAddress
      };
    case actions.SET_BANK_CITY:
      return {
        ...state,
        bankCity: action.bankCity
      };
    case actions.SET_BANK_STATE:
      return {
        ...state,
        bankState: action.bankState
      };
    case actions.SET_BANK_ZIP:
      return {
        ...state,
        bankZip: action.bankZip
      };
    case actions.SET_EXP_DATE:
      return {
        ...state,
        expDate: action.expDate
      };
    case actions.SET_SUBMITTED:
      return {
        ...state,
        submitted: action.submitted
      };
    default:
      return state;
  }
};

export default reducer;
