import React from "react";
import InvoiceReceipt from "./InvoiceReceipt";
import ReceiptInvoiceCheckedInput from "./ReceiptInvoiceCheckedInput";
import PaymentBranch from './PaymentBranch'
import AutoPayQuestion from './AutoPayQuestion'


const PaymentMethod = () => {
  return (
    <div>
      <InvoiceReceipt></InvoiceReceipt>
      <ReceiptInvoiceCheckedInput></ReceiptInvoiceCheckedInput>
      <AutoPayQuestion></AutoPayQuestion>
      <PaymentBranch></PaymentBranch>
    </div>
  );
};

export default PaymentMethod;
