import React, { useContext } from "react";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../../RequiredAsterisk";
import BillingSetupContext from "../../BillingSetupContext";
import CCType from "./CCType";
import isEmpty from "../../isEmptyFunction"

const SecurityCode = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  let x = 3; //all except amex use 3 digit sec code
  if (business.ccNumber[0] === "3") {
    x = 4;
  } else {
    x = 3;
  }
  return (
    <div>
      {" "}
      <Paper className="child-app-paper" elevation={3}>
        Please enter your {x} digit security code
        <span>
          {" "}
          for your <CCType></CCType>.
        </span>
        <RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <br></br>
        <Input
          className="business-name"
          placeholder="What is your security code?"
          onChange={e =>
            dispatch({
              type: "SET_SECURITY_CODE",
              securityCode: e.target.value
            })
          }
          value={business.securityCode}
          error={isEmpty(business.securityCode)}
        ></Input>
      </Paper>
    </div>
  );
};
export default SecurityCode;
