import React, { useContext } from "react";
import { Paper } from "@material-ui/core";
import RequiredAsterisk from "../../RequiredAsterisk";
import BillingSetupContext from "../../BillingSetupContext";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from "@material-ui/pickers";

const ExpDate = () => {
  const { business, dispatch } = useContext(BillingSetupContext);

  const handleDateChange = date => {
    dispatch({ type: "SET_EXP_DATE", expDate: date });
  };
  return (
    <div>
      {" "}
      <Paper className="child-app-paper" elevation={3}>
        Credit Card Expiration Date<RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <br></br>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="MM/dd/yyyy"
            margin="normal"
            label="Choose your CC expiration date."
            value={business.expDate}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              "aria-label": "change date"
            }}
          />
        </MuiPickersUtilsProvider>
      </Paper>
    </div>
  );
};
export default ExpDate;
