import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";

const PrimaryContactEmail = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Primary Contact Email (if different than A/P Contact)
      <br></br>
      <Input
        className="main-input"
        placeholder="What is your primary contact's email?"
        onChange={e =>
          dispatch({
            type: "SET_PRIMARY_CONTACT_EMAIL",
            primaryContactEmail: e.target.value
          })
        }
        value={business.primaryContactEmail}
      ></Input>
    </Paper>
  );
};

export default PrimaryContactEmail;
