import React, { useContext } from "react";
import BillingSetupContext from "../../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";

const CCBillingAddress = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Please enter billing address for the card. Please enter Street Name,
      Number, City, State and Zip Code if different than primary contact name
      given earlier.
      <br></br> <br></br>
      Address:<br></br>
      <Input
        className="main-input"
        placeholder="Address"
        onChange={e =>
          dispatch({
            type: "SET_CC_BILLING_ADDRESS",
            ccBillingAddress: e.target.value
          })
        }
        value={business.ccBillingAddress}
      ></Input>
      <br></br><br></br>
      City:<br></br>
      <Input
        className="main-input"
        placeholder="City"
        onChange={e =>
          dispatch({
            type: "SET_CC_BILLING_CITY",
            ccBillingCity: e.target.value
          })
        }
        value={business.ccBillingCity}
      ></Input>
      <br></br><br></br>
      State:<br></br>
      <Input
        className="main-input"
        placeholder="State"
        onChange={e =>
          dispatch({
            type: "SET_CC_BILLING_STATE",
            ccBillingState: e.target.value
          })
        }
        value={business.ccBillingState}
      ></Input>
      <br></br><br></br>
      Zip Code:<br></br>
      <Input
        className="main-input"
        placeholder="Zip Code"
        onChange={e =>
          dispatch({
            type: "SET_CC_BILLING_ZIP",
            ccBillingZip: e.target.value
          })
        }
        value={business.ccBillingZip}
      ></Input>
    </Paper>
  );
};

export default CCBillingAddress;
