import React, { useContext } from "react";
import { Paper, RadioGroup, Radio, FormControlLabel } from "@material-ui/core";
import RequiredAsterisk from "./RequiredAsterisk";
import BillingSetupContext from "./BillingSetupContext";

const UpdateQuestion = () => {
  const { business, dispatch } = useContext(BillingSetupContext);

  return (
    <div>
      <Paper className="child-app-paper" elevation={3}>
        What would you like to update today?
        <RequiredAsterisk></RequiredAsterisk>
        <RadioGroup
          aria-label="update"
          name="update1"
          onChange={e =>
            dispatch({
              type: "SET_UPDATE_PREFERENCE",
              updatePreference: e.target.value
            })
          }
          value={business.updatePreference}
        >
          <FormControlLabel
            value="Billing Contact"
            control={<Radio />}
            label="Billing Contact"
          />
          <FormControlLabel
            value="Payment Method"
            control={<Radio />}
            label="Payment Method"
          />
          <FormControlLabel value="Both" control={<Radio />} label="Both" />
        </RadioGroup>
      </Paper>
    </div>
  );
};
export default UpdateQuestion;
