import React, { useContext } from "react";
import BillingSetupContext from "../../BillingSetupContext";

const CCType = () => {
  const { business } = useContext(BillingSetupContext);
  switch (business.ccNumber[0]) {
    case "3":
      return <span>American Express card</span>;
    case "4":
      return <span>Visa card</span>;
    case "5":
      return <span>MasterCard</span>;
    case "6":
      return <span>Discover card</span>;

    default:
      return <span>card</span>;
  }
};
export default CCType;
