import React, { useContext } from "react";
import BillingSetupContext from "../../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";

const FullName = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Please enter full name as it appears on the card. Not necessary if the name is the same as the contact entered earlier in this form.
      <br></br>
      <br></br>
      <Input
        className="main-input"
        placeholder="Full Name"
        onChange={e =>
          dispatch({
            type: "SET_FULL_NAME",
            fullName: e.target.value
          })
        }
        value={business.fullName}
      ></Input>
    </Paper>
  );
};

export default FullName;
