import React, { useContext } from "react";
import { Paper, RadioGroup, Radio, FormControlLabel } from "@material-ui/core";
import RequiredAsterisk from "../RequiredAsterisk";
import BillingSetupContext from "../BillingSetupContext";

const AutoPayQuestion = () => {
  const { business, dispatch } = useContext(BillingSetupContext);

  return (
    <div>
      <Paper className="child-app-paper" elevation={3}>
        How would you like to use Auto-Pay for your statements?
        <RequiredAsterisk></RequiredAsterisk>
        <br></br><br></br>
        <RadioGroup
          aria-label="autopay"
          name="autopay1"
          onChange={e =>
            dispatch({
              type: "SET_AUTOPAY_PREFERENCE",
              autoPayPreference: e.target.value
            })
          }
          value={business.autoPayPreference}
        >
          <FormControlLabel
            value="Credit Card"
            control={<Radio />}
            label="Credit Card"
          />
          <FormControlLabel value="ACH" control={<Radio />} label="ACH" />
          <FormControlLabel
            value="Check via Phone"
            control={<Radio />}
            label="Check via Phone"
          />
          <FormControlLabel
            value="Auto Bank Draft"
            control={<Radio />}
            label="Auto Bank Draft"
          />
        </RadioGroup>
      </Paper>
    </div>
  );
};
export default AutoPayQuestion;
