import React, { useContext } from "react";
import PaymentMethod from "./PaymentMethodPath/PaymentMethod";
import ContactUpdate from "./BillingContactPath/ContactUpdate";
import BillingSetupContext from "./BillingSetupContext";
import Approval from "./Approval";

const FormBranch = () => {
  const { business } = useContext(BillingSetupContext);
  if (business.updatePreference === "Billing Contact") {
    return (
      <div>
        <ContactUpdate></ContactUpdate>
        <hr></hr>
        <Approval></Approval>
      </div>
    );
  } else if (business.updatePreference === "Payment Method") {
    return (
      <div>
        <PaymentMethod></PaymentMethod>
        <hr></hr>
        <Approval></Approval>
      </div>
    );
  } else if (business.updatePreference === "Both") {
    return (
      <div>
        <ContactUpdate></ContactUpdate>
        <hr></hr>
        <PaymentMethod></PaymentMethod>
        <hr></hr>
        <Approval></Approval>
      </div>
    );
  } else {
    return <div></div>;
  }
};

export default FormBranch;
