import React, { useReducer } from "react";
import { Card } from "@material-ui/core";
import BusinessName from "./BusinessName";
import UpdateQuestion from "./UpdateQuestion";
import BillingSetupFormHeader from "./BillingSetupFormHeader";
import BillingSetupContext from "./BillingSetupContext";
import FormBranch from "./FormBranch";
import initialAppData from "./StateManagment/ApplicationState";
import reducer from "./StateManagment/Reducer";
import SubmitButton from "./SubmitButton";
import Submitted from "./Submitted";

const SetupBilling = () => {
  const [business, dispatch] = useReducer(reducer, initialAppData);
  console.log(business);
  if (business.submitted) {
    return (
      <Card className="main-app-card" elevation={24}>
        <BillingSetupFormHeader></BillingSetupFormHeader>
        <hr></hr>
        <Submitted></Submitted>
      </Card>
    );
  }
  return (
    <div>
      <BillingSetupContext.Provider value={{ business, dispatch }}>
        <Card className="main-app-card" elevation={24}>
          <BillingSetupFormHeader></BillingSetupFormHeader>
          <div className="form-start">

            <BusinessName></BusinessName>
            <UpdateQuestion></UpdateQuestion>
          </div>
          <br></br>
          <hr></hr>
          <FormBranch></FormBranch>
          <SubmitButton></SubmitButton>
        </Card>
      </BillingSetupContext.Provider>
    </div>
  );
};
export default SetupBilling;
