import React, { useContext } from "react";
import { Paper, RadioGroup, Radio, FormControlLabel } from "@material-ui/core";
import RequiredAsterisk from "./RequiredAsterisk";
import BillingSetupContext from "./BillingSetupContext";

const Approval = () => {
  const { business,dispatch } = useContext(BillingSetupContext);

  return (
    <div>
      <Paper className="child-app-paper" elevation={3}>
        By clicking yes below, you confirm that all the billing information and
        payment information is correct. You have not knowingly mislead,
        falsified, or input incorrect information for Call Experts or any
        billing entities resulting in late payments. Late payment fees will
        apply for any payments not received within the terms assigned to you at
        account setup.
        <RequiredAsterisk></RequiredAsterisk>
        <RadioGroup
          aria-label="approval"
          name="approval"
          onChange={e =>
            dispatch({ type: "SET_APPROVAL", approval: e.target.value })
          }
          value={business.approval}
        >
          <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
          <FormControlLabel value="No" control={<Radio />} label="No" />
        </RadioGroup>
      </Paper>
    </div>
  );
};
export default Approval;
