import React, { useContext } from "react";
import CCPath from "./CCPath/CCPath";
import BillingSetupContext from "../BillingSetupContext";
import NonCCPath from "./NonCCPath/NonCCPath";

const PaymentBranch = () => {
  const { business } = useContext(BillingSetupContext);
  console.log(business.AutoPayQuestion)
  if (business.autoPayPreference === "Credit Card") {
    return <CCPath></CCPath>;
  } else {
    return <NonCCPath></NonCCPath>;
  }
};

export default PaymentBranch;
