import React, { useContext } from "react";
import BillingSetupContext from "../../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../../RequiredAsterisk";
import isEmpty from "../../isEmptyFunction";

const RoutingNumber = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Routing Number <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <br></br>
      <Input
        className="main-input"
        placeholder="What is your routing number?"
        onChange={e =>
          dispatch({
            type: "SET_ROUTING_NUMBER",
            routingNumber: e.target.value
          })
        }
        value={business.routingNumber}
        error={isEmpty(business.routingNumber)}
      ></Input>
    </Paper>
  );
};

export default RoutingNumber;
